
import Vue from "vue";
import VueSimpleAlert from "vue-simple-alert";
import { defineComponent, onMounted, ref, watch } from "vue";
import NFTGrid from "@/components/gem-bank/NFTGrid.vue";
import ArrowButton from "@/components/ArrowButton.vue";
import useWallet from "@/composables/wallet";
import useCluster from "@/composables/cluster";
import {
  getNFTMetadataForMany,
  getNFTsByOwner,
  INFT,
} from "@/common/web3/NFTget";
import { initGemBank } from "@/common/gem-bank";
import { Connection, PublicKey, Transaction } from "@solana/web3.js";
import { getListDiffBasedOnMints, removeManyFromList } from "@/common/util";
import { BN, Wallet } from "@project-serum/anchor";
import { findWhitelistProofPDA } from "@gemworks/gem-farm-ts";
import { SignerWalletAdapter } from "@solana/wallet-adapter-base";
import { programs } from "@metaplex/js";

const filterNfts = (nfts: INFT[], rarity: string) => {
  const mapping = {
    "4Y1YbhsX2pFWQB1py3GEx87s1H5pRbsRZbKwveRpfJoC": ["Champion"],
    "FqN3fq8QFsseVcpDnxKCsQvAu3SdfPUZXoSDdn1JucF5": ["Guardian"],
    "5YtEKgzTnQqC5NMWY4yUhUAWSpqheCPxLG5scDoyuEae": ["Sentinel"],
    "LK8BLFSxc9Jk5qpSXnotTFsifCh8fWe69mCfSFEYjXF": ["Commander"],
    "GPBkfvJSpVb5BQvT4nkJ35wFy3yAbJte23jpsmtp6oWw": ["Defender"],
    "3qxewUKp2kED85tARYEWennjDfFDEHwHePJZAAhmRhWa": ["Omega", "Black Hole"],
  } as { [key: string]: string[]; };

  return nfts.filter(nft => {
    try {
      const metadata = (nft.externalMetadata as any);
      if (metadata.symbol != "SDS") {
        return false;
      };
      //if (metadata.name.indexOf("Solardex Sentries") < 0) {
      //  return false;
      //};
      const attributes = metadata.attributes as any[];
      return mapping[rarity].indexOf(attributes.filter(a => a.trait_type == "crest")[0].value) >= 0;
    } catch (e) {
      console.log(e);
    }
    return false;
  });
};

export default defineComponent({
  components: { ArrowButton, NFTGrid },
  props: {
    vault: String,
    rarity: String,
  },
  emits: ["selected-wallet-nft"],
  setup(props, ctx) {
    const { wallet, getWallet } = useWallet();
    const { cluster, getConnection } = useCluster();

    // --------------------------------------- state

    //current walet/vault state
    const currentWalletNFTs = ref<INFT[]>([]);
    const currentVaultNFTs = ref<INFT[]>([]);
    //selected but not yet moved over in FE
    const selectedWalletNFTs = ref<INFT[]>([]);
    const selectedVaultNFTs = ref<INFT[]>([]);
    //moved over in FE but not yet onchain
    const desiredWalletNFTs = ref<INFT[]>([]);
    const desiredVaultNFTs = ref<INFT[]>([]);
    //moved over onchain
    const toWalletNFTs = ref<INFT[]>([]);
    const toVaultNFTs = ref<INFT[]>([]);

    // --------------------------------------- populate initial nfts

    const populateWalletNFTs = async () => {
      // zero out to begin with
      currentWalletNFTs.value = [];
      selectedWalletNFTs.value = [];
      desiredWalletNFTs.value = [];

      if (getWallet()) {
        currentWalletNFTs.value = await getNFTsByOwner(
          getWallet()!.publicKey!,
          getConnection()
        );
        currentWalletNFTs.value = filterNfts(currentWalletNFTs.value, props.rarity!);

        desiredWalletNFTs.value = [...currentWalletNFTs.value];
      }
    };

    const populateVaultNFTs = async () => {
      // zero out to begin with
      currentVaultNFTs.value = [];
      selectedVaultNFTs.value = [];
      desiredVaultNFTs.value = [];

      const foundGDRs = await gb.fetchAllGdrPDAs(vault.value);
      if (foundGDRs && foundGDRs.length) {
        gdrs.value = foundGDRs;
        console.log(`found a total of ${foundGDRs.length} gdrs`);

        const mints = foundGDRs.map((gdr: any) => {
          return { mint: gdr.account.gemMint };
        });
        currentVaultNFTs.value = await getNFTMetadataForMany(
          mints,
          getConnection()
        );
        currentVaultNFTs.value = filterNfts(currentVaultNFTs.value, props.rarity!);
        desiredVaultNFTs.value = [...currentVaultNFTs.value];
        console.log(
          `populated a total of ${currentVaultNFTs.value.length} vault NFTs`
        );
      }
    };

    const updateVaultState = async () => {
      vaultAcc.value = await gb.fetchVaultAcc(vault.value);
      console.log("vault--------->", vaultAcc.value);
      bank.value = vaultAcc.value.bank;
      vaultLocked.value = vaultAcc.value.locked;
    };

    watch([wallet, cluster], async () => {
      gb = await initGemBank(getConnection(), getWallet()!);

      //populate wallet + vault nfts
      await Promise.all([populateWalletNFTs(), populateVaultNFTs()]);
    });

    onMounted(async () => {
      gb = await initGemBank(getConnection(), getWallet()!);

      //prep vault + bank variables
      vault.value = new PublicKey(props.vault!);
      await updateVaultState();

      //populate wallet + vault nfts
      await Promise.all([populateWalletNFTs(), populateVaultNFTs()]);
    });

    // --------------------------------------- moving nfts

    const handleWalletSelected = (e: any) => {
      if (e.selected) {
        selectedWalletNFTs.value.push(e.nft);
      } else {
        const index = selectedWalletNFTs.value.indexOf(e.nft);
        selectedWalletNFTs.value.splice(index, 1);
      }
      ctx.emit("selected-wallet-nft", selectedWalletNFTs.value);
    };

    const handleVaultSelected = (e: any) => {
      if (e.selected) {
        selectedVaultNFTs.value.push(e.nft);
      } else {
        const index = selectedVaultNFTs.value.indexOf(e.nft);
        selectedVaultNFTs.value.splice(index, 1);
      }
    };

    const moveNFTsFE = (moveLeft: boolean) => {
      if (moveLeft) {
        //push selected vault nfts into desired wallet
        desiredWalletNFTs.value.push(...selectedVaultNFTs.value);
        //remove selected vault nfts from desired vault
        removeManyFromList(selectedVaultNFTs.value, desiredVaultNFTs.value);
        //empty selection list
        selectedVaultNFTs.value = [];
      } else {
        //push selected wallet nfts into desired vault
        desiredVaultNFTs.value.push(...selectedWalletNFTs.value);
        //remove selected wallet nfts from desired wallet
        removeManyFromList(selectedWalletNFTs.value, desiredWalletNFTs.value);
        //empty selected walelt
        selectedWalletNFTs.value = [];
      }
      setTimeout(() => moveNFTsOnChain(), 1000);
    };

    const moveAllNFTsFE = (moveLeft: boolean) => {
      if (moveLeft) {
        selectedVaultNFTs.value = desiredVaultNFTs.value;
        //push selected vault nfts into desired wallet
        desiredWalletNFTs.value.push(...selectedVaultNFTs.value);
        //remove selected vault nfts from desired vault
        // removeManyFromList(selectedVaultNFTs.value, desiredVaultNFTs.value);
        desiredVaultNFTs.value = [];
        //empty selection list
        selectedVaultNFTs.value = [];
      } else {
        selectedWalletNFTs.value = desiredWalletNFTs.value;
        //push selected wallet nfts into desired vault
        desiredVaultNFTs.value.push(...selectedWalletNFTs.value);
        //remove selected wallet nfts from desired wallet
        // removeManyFromList(selectedWalletNFTs.value, desiredWalletNFTs.value);
        desiredWalletNFTs.value = [];
        //empty selected walelt
        selectedWalletNFTs.value = [];
      }
      setTimeout(() => moveNFTsOnChain(), 1000);
    };

    //todo jam into single tx
    const moveNFTsOnChain = async () => {
      let depositArr: { mint: PublicKey, creator: PublicKey, source: PublicKey }[] = [];
      for (const nft of toVaultNFTs.value) {
        const creator = new PublicKey(
          //todo currently simply taking the 1st creator
          (nft.onchainMetadata as any).data.creators[0].address
        );
        console.log("creator is", creator.toBase58());
        depositArr = [...depositArr, { mint: nft.mint, creator, source: nft.pubkey! }];
      }
      if (depositArr.length > 0) {
        await depositMultipleGem(getWallet()!, getConnection(), depositArr);
      }
      let withdrawArr: PublicKey[] = [];
      for (const nft of toWalletNFTs.value) {
        withdrawArr = [...withdrawArr, nft.mint];
      }
      if (withdrawArr.length > 0) {
        await withdrawMultipleGem(getWallet()!, getConnection(), withdrawArr);
      }
      await Promise.all([populateWalletNFTs(), populateVaultNFTs()]);
    };

    //to vault = vault desired - vault current
    watch(
      desiredVaultNFTs,
      () => {
        toVaultNFTs.value = getListDiffBasedOnMints(
          desiredVaultNFTs.value,
          currentVaultNFTs.value
        );
        console.log("to vault nfts are", toVaultNFTs.value);
      },
      { deep: true }
    );

    //to wallet = wallet desired - wallet current
    watch(
      desiredWalletNFTs,
      () => {
        toWalletNFTs.value = getListDiffBasedOnMints(
          desiredWalletNFTs.value,
          currentWalletNFTs.value
        );
        console.log("to wallet nfts are", toWalletNFTs.value);
      },
      { deep: true }
    );

    // --------------------------------------- gem bank

    let gb: any;
    const bank = ref<PublicKey>();
    const vault = ref<PublicKey>();
    const vaultAcc = ref<any>();
    const gdrs = ref<PublicKey[]>([]);
    const vaultLocked = ref<boolean>(false);

    const depositGem = async (
      mint: PublicKey,
      creator: PublicKey,
      source: PublicKey
    ) => {
      const { txSig } = await gb.depositGemWallet(
        bank.value,
        vault.value,
        new BN(1),
        mint,
        source,
        creator
      );
      VueSimpleAlert.alert("deposit done");
      console.log("deposit done", txSig);
    };

    const withdrawGem = async (mint: PublicKey) => {
      const { txSig } = await gb.withdrawGemWallet(
        bank.value,
        vault.value,
        new BN(1),
        mint
      );
      VueSimpleAlert.alert("withdrawal done");
      console.log("withdrawal done", txSig);
    };

    const depositMultipleGem = async (
      wallet: SignerWalletAdapter,
      connection: Connection,
      arr: { mint: PublicKey, creator: PublicKey, source: PublicKey }[]
    ) => {
      await depositMultipleGemWallet(
        wallet,
        connection,
        bank.value!,
        vault.value!,
        new BN(1),
        arr
      );
      VueSimpleAlert.alert("deposit done");
    };

    const depositMultipleGemWallet = async (
      wallet: SignerWalletAdapter,
      connection: Connection,
      bank: PublicKey,
      vault: PublicKey,
      gemAmount: BN,
      arr: { mint: PublicKey, creator: PublicKey, source: PublicKey }[]
    ) => {
      const txArr = [];
      const blockhashObj = await connection.getRecentBlockhash();
      const recentBlockhash = await blockhashObj.blockhash;
      for (const gem of arr) {
        const [mintProof, bump] = await findWhitelistProofPDA(bank, gem.mint);
        const [creatorProof, bump2] = await findWhitelistProofPDA(bank, gem.creator);
        const metadata = await programs.metadata.Metadata.getPDA(gem.mint);

        const {
          builder,
        } = await gb.buildDepositGem(
          bank,
          vault,
          wallet.publicKey,
          gemAmount,
          gem.mint,
          gem.source,
          mintProof,
          metadata,
          creatorProof
        );

        const tx = new Transaction();
        tx.feePayer = wallet.publicKey!;
        tx.recentBlockhash = recentBlockhash;
        tx.add(await builder.instruction());
        txArr.push(tx);
      }

      const signedTx = await wallet.signAllTransactions(txArr);
      for (const tx of signedTx) {
          const signature = await connection.sendRawTransaction(tx.serialize());
          await connection.confirmTransaction(signature);
      }
    }

    const withdrawMultipleGem = async (
      wallet: SignerWalletAdapter,
      connection: Connection,
      arr: PublicKey[]
    ) => {
      await withdrawMultipleGemWallet(
        wallet,
        connection,
        bank.value!,
        vault.value!,
        new BN(1),
        arr
      );
      VueSimpleAlert.alert("withdrawal done");
    };

    const withdrawMultipleGemWallet = async (
      wallet: SignerWalletAdapter,
      connection: Connection,
      bank: PublicKey,
      vault: PublicKey,
      gemAmount: BN,
      arr: PublicKey[]
    ) => {
      const txArr = [];
      const blockhashObj = await connection.getRecentBlockhash();
      const recentBlockhash = await blockhashObj.blockhash;
      for (const gem of arr) {
        const {
          builder,
        } = await gb.buildWithdrawGem(bank, vault, wallet.publicKey, gemAmount, gem, wallet.publicKey);

        const tx = new Transaction();
        tx.feePayer = wallet.publicKey!;
        tx.recentBlockhash = recentBlockhash;
        tx.add(await builder.instruction());
        txArr.push(tx);
      }

      const signedTx = await wallet.signAllTransactions(txArr);
      for (const tx of signedTx) {
          const signature = await connection.sendRawTransaction(tx.serialize());
          await connection.confirmTransaction(signature);
      }
    }

    // --------------------------------------- return

    return {
      wallet,
      desiredWalletNFTs,
      desiredVaultNFTs,
      toVaultNFTs,
      toWalletNFTs,
      handleWalletSelected,
      handleVaultSelected,
      moveNFTsFE,
      moveAllNFTsFE,
      moveNFTsOnChain,
      bank,
      // eslint-disable-next-line vue/no-dupe-keys
      vault,
      vaultLocked,
    };
  },
});
